* {
  text-decoration: none;
}

html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  background-color: skyblue;
  text-align: center;

  display: flex;
  justify-content: center;
}

.card {
  width: 95%;
  margin: 10px;
  background-color: beige;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
  border-radius: 10px;
}

.card:hover {
  transform: scale(1.05);
}

.detailCard {
  background-color: beige;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
  margin: auto;
  max-width: 800px;
  padding: 20px;
  border-radius: 10px;
}

ul {
  list-style-type: none; /* 불릿 마커 없애기 */
  text-align: center; /* 텍스트 가운데 정렬 */
}

/* 선택적으로 마커를 다른 모양으로 바꾸려면 아래와 같이 사용할 수 있습니다 */
li:before {
  content: " "; /* 다른 마커로 대체, 여기서는 원형 마커를 사용합니다 */
  margin-right: 20px; /* 마커와 텍스트 사이의 간격 조절 */
}
