.title {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 70px;
}

/* 각 카드 사이의 간격 조절을 위한 마진 추가 */
.cardGrid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(700px, 1fr));
  gap: 20px;
}

.cardContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
